import { BASE_URL } from "../constants"
import { BusinessUnitConfigCenterline, CenterlineData, CenterlineEntity, CenterlineInsertEntity, CenterlineUpdateEntity, DashboardCenterlineLevel1, DashboardCenterlineLevel2, DashboardCenterlineLevel3, FormBusinessUnitCenterline, FormLinkupCenterline, LinkUpConfigCenterline, } from "../entities/CenterlineEntity"
import FetchUtils from "../utils/fetch"

class CenterlineService {
    static async GetAllCenterline(link_up_id?: string): Promise<CenterlineEntity[]> {

        let uri = `${BASE_URL}/api/extension/centerline/v1`
        if (link_up_id != null) {
            uri += `?link_up_id=${link_up_id}`
        }

        const resp = await FetchUtils.fetchAuth(uri)
        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        const data: CenterlineEntity[] = Array.isArray(resp.data) ? resp.data : []
        for (let i = 0; i < data.length; i++) {
            if (typeof data[i].centerline_id != "number") { throw new Error("invalid centerline_id value") }
            if (typeof data[i].link_up_id != "number") { throw new Error("invalid link_up_id value") }
            if (typeof data[i].link_up != "string") { throw new Error("invalid link_up value") }
            if (typeof data[i].parameter_name != "string") { throw new Error("invalid parameter_name value") }
            if (typeof data[i].centerline_tipe != "string") { throw new Error("invalid centerline_tipe value") }
            if (data[i].centerline_warn != undefined && typeof data[i].centerline_warn != "number") { throw new Error("invalid centerline_warn value") }
            if (data[i].centerline_alarm != undefined && typeof data[i].centerline_alarm != "number") { throw new Error("invalid centerline_alarm value") }
            if (data[i].centerline_bottom_warn != undefined && typeof data[i].centerline_bottom_warn != "number") { throw new Error("invalid centerline_bottom_warn value") }
            if (data[i].centerline_bottom_alarm != undefined && typeof data[i].centerline_bottom_alarm != "number") { throw new Error("invalid centerline_bottom_alarm value") }
            if (typeof data[i].centerline_display_type != "string") { throw new Error("invalid centerline_display_type value") }
            if (typeof data[i].centerline_unit != "string") { throw new Error("invalid centerline_unit value") }
        }

        return data
    }

    static async AddCenterline(data: CenterlineInsertEntity): Promise<{ message: string }> {
        const resp = await FetchUtils.fetchAuth(`${BASE_URL}/api/extension/centerline/v1`,
            {
                method: 'POST',
                body: JSON.stringify({ ...data }),
                headers: { "Content-Type": "application/json" }
            }
        )

        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        return resp
    }

    static async UpdateCenterline(centerline_id: number, fields: CenterlineUpdateEntity): Promise<{ message: string }> {
        const resp = await FetchUtils.fetchAuth(`${BASE_URL}/api/extension/centerline/v1`,
            {
                method: 'PATCH',
                body: JSON.stringify({ centerline_id, fields }),
                headers: { "Content-Type": "application/json" }
            }
        )

        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        return resp
    }

    static async DeleteCenterline(centerline_id: number): Promise<{ message: string }> {
        const resp = await FetchUtils.fetchAuth(`${BASE_URL}/api/extension/centerline/v1`,
            {
                method: 'DELETE',
                body: JSON.stringify({ centerline_id }),
                headers: { "Content-Type": "application/json" }
            }
        )

        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        return resp
    }

    static async GetLinkUpConfig(link_up_id?: number): Promise<LinkUpConfigCenterline[]> {
        let uri = `${BASE_URL}/api/extension/centerline/v1/linkup`
        if (link_up_id != null) {
            uri += `?link_up_id=${link_up_id}`
        }

        const resp = await FetchUtils.fetchAuth(uri)
        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        if (!Array.isArray(resp.data)) {
            throw new Error("invalid link up config response")
        }
        for (let i = 0; i < resp.data.length; i++) {
            const row = resp.data[i]
            if (typeof row.link_up_id != "number") { throw new Error("invalid link_up_id value") }
            if (typeof (row.centerline_warn ?? 0) != "number") { throw new Error("invalid centerline_warn value") }
            if (typeof (row.centerline_alarm ?? 0) != "number") { throw new Error("invalid centerline_alarm value") }
        }

        return resp.data
    }

    static async UpsertLinkupConfig(link_up_id: number, fields: FormLinkupCenterline): Promise<{ message: string }> {
        const resp = await FetchUtils.fetchAuth(`${BASE_URL}/api/extension/centerline/v1/linkup`, {
            method: 'PATCH',
            body: JSON.stringify({ link_up_id, fields }),
            headers: { "Content-Type": "application/json" }
        })

        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        return resp
    }

    static async GetBusinessUnitConfig(business_unit_id?: number): Promise<BusinessUnitConfigCenterline[]> {
        let uri = `${BASE_URL}/api/extension/centerline/v1/business`
        if (business_unit_id != null) {
            uri += `?business_unit_id=${business_unit_id}`
        }

        const resp = await FetchUtils.fetchAuth(uri)
        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        if (!Array.isArray(resp.data)) {
            throw new Error("invalid link up config response")
        }
        for (let i = 0; i < resp.data.length; i++) {
            const row = resp.data[i]
            if (typeof row.business_unit_id != "number") { throw new Error("invalid business_unit_id value") }
            if (typeof (row.centerline_warn ?? 0) != "number") { throw new Error("invalid centerline_warn value") }
            if (typeof (row.centerline_alarm ?? 0) != "number") { throw new Error("invalid centerline_alarm value") }
        }

        return resp.data
    }

    static async UpsertBusinessUnitConfig(business_unit_id: number, fields: FormBusinessUnitCenterline): Promise<{ message: string }> {
        const resp = await FetchUtils.fetchAuth(`${BASE_URL}/api/extension/centerline/v1/business`, {
            method: 'PATCH',
            body: JSON.stringify({ business_unit_id, fields }),
            headers: { "Content-Type": "application/json" }
        })

        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        return resp
    }
}

